@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body{background:linear-gradient(45deg, #333 0%, #AEA79F 100%);font-family:'Ubuntu';margin:0}.container{display:flex;justify-content:center;align-items:center;height:100vh;margin-bottom:-30px}.terminal{width:80%;height:60%;box-shadow:2px 4px 10px rgba(0,0,0,0.5);margin:10px}.terminal_body{background:rgba(94,39,80,0.9);height:calc(100%);margin-top:-1px;padding:10px;font-family:'Ubuntu mono'}.terminal_text{color:#ddd}.terminal_prompt{margin-top:2px;display:flex}.terminal_prompt_output{margin-top:2px;display:flex;color:lime}.prompt{white-space:nowrap}.prompt_user{color:#f6f6f5}.prompt_location{color:#6d85a9}.prompt_icon{color:#f6f6f5}.prompt_text{color:#f6f6f5;margin-left:8px;white-space:nowrap}.prompt_cursor{height:17px;width:7px;background:rgba(255,255,255,0.772);margin-left:8px;-webkit-animation:1000ms blink alternate infinite;animation:1000ms blink alternate infinite}.footer{background:linear-gradient(#504b45 0%, #3c3b37 100%);height:30px;display:flex;justify-content:center;align-items:center}.footer_text{color:#f6f6f5;font-size:12px;line-height:14px}a{color:#41b7d4}@-webkit-keyframes blink{0%{opacity:0}100%{opacity:1}}@keyframes blink{0%{opacity:0}100%{opacity:1}}

